<template>
  <div>
    <h4 style="">
      <span style="color: red" v-if="question.necessary == true">* </span>
      {{ question.title }}：
    </h4>
    <p>{{ question.description}}</p>
    <v-textarea
      style="width: 30vw; margin-left: 0vw; margin-top: 1vh"
      dense
      solo
      @input="update"
    ></v-textarea>
  </div>
</template>

<script>
import "@/assets/font/font.css";
export default {
  name: "FillBlank",
  props: {
    value: {
      type: Object,
    },
  },
  mounted() {
    this.question = this.value;
  },
  model: {
    prop: "value",
    event: "selection",
  },
  data() {
    return {
      question: {},
    };
  },
  methods: {
    update() {
      this.$emit("selection", this.question);
      //console.log(this.choice);
    },
  },
};
</script>
