<template>
  <div class="EachTag">
    <v-expansion-panels accordion multiple>
            <v-expansion-panel v-for="(item,i) in titleList" :key="item.id">
              <v-expansion-panel-header style="font-size:15px">
                {{item.title}}
              </v-expansion-panel-header>
              <v-expansion-panel-content  style="font-size:7px">
                <v-chip-group
                  v-model="choices[i]"
                  column
                  multiple
                >
                <v-chip  outlined color="primary" small @click="update(question)"
                v-for="question in item.questionList" :key="question.id" >
                  {{question.question}}
                </v-chip>
            </v-chip-group>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
  </div>
</template>

<script>
import "@/assets/font/font.css"
export default {
  name: "LeftBar",
  props:{
    value: {
      type: String,
    },
        titleList:{
                type:Array,
                default:[],
                required:true
            }
    },
    model: {
      prop: "value",
      event: "selection",
    },
  data(){
    return{
      choices:[],
      index:0,
      choice:[]
    }
  },
  methods:{
    update(question) {
      this.choice.push(question.question)
      this.$emit("selection",this.choice[this.choice.length-1]+this.index);
      this.index++
    },
  }
}
</script>
<style scoped>
.EachTag{
  width:100%;
  margin: 0 auto;
  position: relative;
}
</style>